import Vue from 'vue'
import Router from 'vue-router'
import AutoWriting from '@/components/AutoWriting'

Vue.use(Router)

//  ルーティング

export default new Router({
  routes: [
    {
      path: '/',
      name: 'AutoWriting',
      component: AutoWriting
    },
  ]
})



