<template>
  <div class="AutoWritingMain">
    <img src="../assets/logo.png">
    <p>{{ msg }}</p>
    <button @click="startSpeech">{{ recognitionText }}</button>
    <div id="content">
      <ul>
        <li v-for="(todo,index) in todos" :key="index">
          <label @click="todo.edit = true">{{todo.text}}</label>
          <div @click="delClick(todo,$event)">✖</div>
          <textarea
            v-if="todo.edit"
            type="text"
            v-model="todo.text"
            @blur="blurEvent(todo,$event)"
            :rows="todo.text.split(/\n/).length"
            :cols="100"
            auto-focus
          ></textarea>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
// textareaの幅うまくいかなかった例、((todo.text.split(/\n/)).reduce(function(a,b) {a.length>b.length? a.length : b.length}))

// Vue.directive
// ディレクティブとは、 DOM 要素に対して何かを実行することをライブラリに伝達する、マークアップ中の特別なトークンです。
// Vue.directiveはその中でも開発者自身が独自に設定するものになります。

// Vue.nextTick
// callback を延期し、DOM の更新サイクル後に実行します。
// DOM 更新を待ち受けるために、いくつかのデータを変更した直後に使用してください。
import Vue from 'vue'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'スタートを押すと音声認識を開始します。\nもう一回押すと止まります。',
      todos: [],
      recognition: new webkitSpeechRecognition(),
      recognitionText: 'スタート',
      activeFlg: '0'
    }
  },
  methods: {
    startSpeech: function () {
      if (this.activeFlg === '0') {
        this.activeFlg = '1'
        this.recognition.start()
      } else {
        this.activeFlg = '0'
        this.recognition.stop()
      }
    },
    blurEvent: function (todo, event) {
      var todos = this.todos
      var index = todos.indexOf(todo)
      if (todo.text === '') {
        todos.splice(index, 1)
      } else {
        todos[index].edit = false
      }
    },
    delClick: function (todo, event) {
      var todos = this.todos
      var index = todos.indexOf(todo)
      todos.splice(index, 1)
    }
  },
  created: function () {
    this.recognition.onstart = () => {
      this.recognitionText = '音声入力中...'
    }
    this.recognition.onend = () => {
      this.recognitionText = 'スタート'
      if (this.activeFlg === '1') {
        this.recognition.start()
      }
    }
    this.recognition.onresult = (event) => {
      this.recognition.stop()
      if (event.results.length > 0) {
        this.todos.push(
          { text: event.results[0][0].transcript, edit: false }
        )
      }
    }
  }
}

Vue.directive('auto-focus', {
  bind: function () {
    var el = this.el
    Vue.nextTick(function () {
      el.focus()
    })
  }
})
</script>

<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

#content ul li {
  display: block;
  margin: auto;
  color: rgb(32, 30, 32);
}
#content ul li label {
  white-space: pre-wrap;
}
#content ul li textarea {
  display: block;
  margin: auto;
}
#content ul li div {
  display: inline;
  font-weight: bold;
  font-style: italic;
  background-color: darkgray;
  border: inset 2px black;
}
.AutoWritingMain p {
  white-space: pre-wrap;
}
</style>



